import "./styles.css";
import moemoemennko from "./moemoemennko";
import * as React from "react";
import Checkbox from "@mui/material/Checkbox";

const label = { inputProps: { "aria-label": "Checkbox demo" } };

function moemoemennkos(index) {
  const items = [];
  const keys = Object.keys(moemoemennko[index]);
  const values = Object.values(moemoemennko[index]);
  for (let i = 0; i < values.length; i++) {
    items.push(
      <li>
        {keys[i]}:{values[i]}
      </li>
    );
  }
  return <ul>{items}</ul>;
}

export default function App() {
  return (
    <>
      <h1>メニュー</h1>
      {moemoemennkos(0)}
      <Checkbox {...label} />
      {moemoemennkos(1)}
      <Checkbox {...label} />
      {moemoemennkos(2)}
      <Checkbox {...label} />
    </>
  );
}
